import React from "react";

import BusinessCenterOutlinedIcon from "@mui/icons-material/BusinessCenterOutlined";
import Box from "@mui/material/Box";
import Avatar from "@mui/material/Avatar";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import { get } from "lodash";

import SwitchToggle from "../../ui-components/switchToggle/SwitchToggle";
import moment from "moment";

const LicenseCertificationData = (props) => {
  const { consultantInfo, loading, infoLoading, handleSectionChildStatus } =
    props;

    console.log(consultantInfo,'consultantInfo')
  return (
    <>
      {get(consultantInfo, "licenseCertification", []).map((item, index) => (
        <Box key={index}>
          <Stack
            direction="row"
            align="center"
            justifyContent="space-between"
            spacing={2}
            py={2}
          >
            <Stack direction="row" align="center">
              <Box component="span">
                <Avatar
                  sx={{
                    backgroundColor: "grey.300",
                    color: "text.secondary",
                  }}
                >
                  <BusinessCenterOutlinedIcon />
                </Avatar>
              </Box>
              <Box sx={{ textAlign: "start", marginLeft: "10px" }}>
                <Typography
                  variant="body2"
                  color="text.primary"
                  className="fw-bold"
                >
                  {`${item.name} at ${item.issuingOrganization}`}
                </Typography>
                <Typography variant="caption" color="text.secondary">
                  {`${moment(item.issueDate).format("L")} - ${
                    item.credentialId
                  } - ${item.credentialUrl}`}
                </Typography>
              </Box>
            </Stack>
            <SwitchToggle
              checked={get(item, "status", true)}
              onChange={(e) =>
                handleSectionChildStatus(e, "licenseCertification", item._id)
              }
              disabled={
                loading ||
                infoLoading ||
                !get(consultantInfo, "licenseCertificationStatus", false)
              }
              inputProps={{
                "aria-label": `licenseCertification-${index}-status`,
                role: "switch",
              }}
            />
          </Stack>
        </Box>
      ))}
    </>
  );
};

export default LicenseCertificationData;
