import React from "react";
import Box from "@mui/material/Box";
import Avatar from "@mui/material/Avatar";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import { get } from "lodash";
import SwitchToggle from "../../ui-components/switchToggle/SwitchToggle";
import TwitterIcon from "@mui/icons-material/Twitter";
import FacebookIcon from "@mui/icons-material/Facebook";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import InstagramIcon from "@mui/icons-material/Instagram";
import YouTubeIcon from "@mui/icons-material/YouTube";
import BusinessCenterOutlinedIcon from "@mui/icons-material/BusinessCenterOutlined";

const SocialLinksData = (props) => {
  const { consultantInfo, loading, infoLoading, handleSectionChildStatus } =
    props;

  const getIconComponent = (socialMediaChanel) => {
    switch (socialMediaChanel) {
      case "Twitter":
        return <TwitterIcon />;
      case "Facebook":
        return <FacebookIcon />;
      case "LinkedIn":
        return <LinkedInIcon />;
      case "Instagram":
        return <InstagramIcon />;
      case "YouTube":
        return <YouTubeIcon />;
      default:
        return <BusinessCenterOutlinedIcon />;
    }
  };

  return (
    <>
      {get(consultantInfo, "socialMediaInfo", []).map((item, index) => (
        <Box key={index}>
          <Stack
            direction="row"
            alignItems="center"
            justifyContent="space-between"
            spacing={1}
            mb={1}
          >
            <Stack direction="row" alignItems="center" spacing={1}>
              <Box component="span">
                <Avatar
                  sx={{
                    backgroundColor: "grey.300",
                    color: "text.secondary",
                  }}
                >
                  {getIconComponent(item.socialMediaChanel)}
                </Avatar>
              </Box>
              <Box component="div">
                <Typography
                  variant="body2"
                  color="text.primary"
                  className="fw-bold"
                >
                  Follow me on {item.socialMediaChanel}
                </Typography>
                <Typography
                  color="primary.main"
                  variant="caption"
                  component="span"
                  sx={{ wordBreak: "break-all" }}
                >
                  {item.chanelLink}
                </Typography>
              </Box>
            </Stack>
            <SwitchToggle
              checked={get(item, "status", true)}
              onChange={(e) =>
                handleSectionChildStatus(e, "socialMediaInfo", item._id)
              }
              disabled={
                loading ||
                infoLoading ||
                !get(consultantInfo, "socialMediaInfoStatus", false)
              }
              inputProps={{
                "aria-label": `socialMediaInfo-${index}-status`,
                role: "switch",
              }}
            />
          </Stack>
        </Box>
      ))}
    </>
  );
};

export default SocialLinksData;
