import React, { useState } from "react";

import VisibilityIcon from "@mui/icons-material/Visibility";
import Chip from "@mui/material/Chip";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import { get, map } from "lodash";

import { CategoryDesign } from "../../Icon";
import CMAccordion from "../../ui-components/accordion/CMAccordion";
import CMAccordionDetails from "../../ui-components/accordion/CMAccordionDetails";
import CMAccordionSummary from "../../ui-components/accordion/CMAccordionSummary";
import SwitchToggle from "../../ui-components/switchToggle/SwitchToggle";
import translate from "../../hooks/translate";

const ProfessionalDomain = (props) => {
  const {
    consultantInfo,
    loading,
    infoLoading,
    handleSectionChildStatus,
    handleSectionStatus,
  } = props;

  const [expanded, setExpanded] = useState("panel0");
  const handleChange = (panel) => (_event, newExpanded) => {
    setExpanded(newExpanded ? panel : false);
  };

  console.log(consultantInfo,'consultantInfo')
  return (
    <>
      <Paper variant="outlined" sx={{ my: 1, px: 2, py: 1 }}>
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Typography variant="subtitle1">
            {translate("consultant_specializations_and_sub_specializations")}
          </Typography>
          <SwitchToggle
            checked={get(consultantInfo, "specializationStatus", true)}
            onChange={(e) => handleSectionStatus(e, "specialization")}
            disabled={loading || infoLoading}
            inputProps={{
              "aria-label": `specialization-status`,
              role: "switch",
            }}
          />
        </Stack>

        <Grid item xs={12}>
          {map(get(consultantInfo, "specialization", []), (item, index) => (
            console.log(item,'58'),
            <CMAccordion
              expanded={expanded === `panel${index}`}
              onChange={handleChange(`panel${index}`)}
              key={index}
            >
              <CMAccordionSummary
                expandIcon={<CategoryDesign sx={{ margin: 0 }} />}
                reverse={true}
              >
                <Typography style={{fontWeight:"600"}}>
                  {translate(get(item, "foreignData.macroName", ""))}
                </Typography>
                <SwitchToggle
                  checked={get(item, "status", true)}
                  onChange={(e) =>
                    handleSectionChildStatus(e, "specialization", item._id)
                  }
                  disabled={
                    loading ||
                    infoLoading ||
                    !get(consultantInfo, "specializationStatus", false)
                  }
                  inputProps={{
                    "aria-label": `specialization-status`,
                    role: "switch",
                  }}
                />
              </CMAccordionSummary>

              <CMAccordionDetails>
                {map(item.child, (itemChip) => (
                  <Chip
                    key={itemChip._id}
                    label={translate(
                      get(itemChip, "foreignData.macroName", "")
                    )}
                    onClick={(e) =>
                      handleSectionChildStatus(e, "child", item._id)
                    }
                    disabled={!item.status}
                    icon={<VisibilityIcon />}
                    sx={{
                      fontSize: "14px",
                      color: "text.primary",
                      mb: 1,
                      mr: 1,
                    }}
                  />
                ))}
              </CMAccordionDetails>
            </CMAccordion>
          ))}
        </Grid>
      </Paper>
    </>
  );
};

export default ProfessionalDomain;
