import React from "react";

import Avatar from "@mui/material/Avatar";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import IconButton from "@mui/material/IconButton";
import Link from "@mui/material/Link";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import { get, upperCase } from "lodash";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { toast } from "react-toastify";

import Iconify from "../../common/Iconify";
import SwitchToggle from "../../ui-components/switchToggle/SwitchToggle";
import translate from "../../hooks/translate";

const TopDetails = (props) => {
  const { consultantInfo, handleStatus, loading, infoLoading, secName } = props;
  const FRONTEND_END_POINT = process.env.REACT_APP_FRONTEND_END_POINT;

  return (
    <>
      <Box>
        <Grid container>
          <Grid item md={1.7} xs={12}>
            <Avatar
              sx={{
                height: { xs: "200px", md: "100px" },
                width: { xs: "200px", md: "100px" },
                backgroundColor: "primary.lighter",
                margin: { xs: "auto", md: "0" },
              }}
              alt={upperCase(get(consultantInfo, "firstName", "U"))}
              src={get(consultantInfo, "profilePictureURL", "NA")}
            />
          </Grid>

          <Grid item md={10.3} xs={12}>
            <Grid container spacing={2}>
              <Grid item md={6} xs={12}>
                <Stack
                  direction="row"
                  justifyContent="space-between"
                  alignItems="center"
                >
                  <Typography variant="subtitle1">
                    {consultantInfo.firstName}
                  </Typography>
                  <SwitchToggle
                    checked={get(consultantInfo, "firstNameStatus", true)}
                    onChange={(e) => handleStatus(e, "firstNameStatus")}
                    disabled={
                      secName === "firstNameStatus"
                        ? loading || infoLoading
                        : false
                    }
                    inputProps={{
                      "aria-label": `first-Name-status`,
                      role: "switch",
                    }}
                  />
                </Stack>

                <Stack
                  direction="row"
                  justifyContent="space-between"
                  alignItems="center"
                >
                  <Typography variant="subtitle1">
                    {consultantInfo.email}
                  </Typography>
                  <SwitchToggle
                    checked={get(consultantInfo, "emailStatus", true)}
                    onChange={(e) => handleStatus(e, "emailStatus")}
                    disabled={
                      secName === "emailStatus" ? loading || infoLoading : false
                    }
                    inputProps={{
                      "aria-label": `email-status`,
                      role: "switch",
                    }}
                  />
                </Stack>
              </Grid>

              <Grid item md={6} xs={12}>
                <Stack
                  direction="row"
                  justifyContent="space-between"
                  alignItems="center"
                >
                  <Typography variant="subtitle1">
                    {consultantInfo.lastName}
                  </Typography>
                  <SwitchToggle
                    checked={get(consultantInfo, "lastNameStatus", true)}
                    onChange={(e) => handleStatus(e, "lastNameStatus")}
                    disabled={
                      secName === "lastNameStatus"
                        ? loading || infoLoading
                        : false
                    }
                    inputProps={{
                      "aria-label": `last-name-status`,
                      role: "switch",
                    }}
                  />
                </Stack>

                <Stack
                  direction="row"
                  justifyContent="space-between"
                  alignItems="center"
                >
                  <Typography variant="subtitle1">
                    {consultantInfo.mobile}
                  </Typography>
                  <SwitchToggle
                    checked={get(consultantInfo, "mobileStatus", true)}
                    onChange={(e) => handleStatus(e, "mobileStatus")}
                    disabled={
                      secName === "mobileStatus"
                        ? loading || infoLoading
                        : false
                    }
                    inputProps={{
                      "aria-label": `mobile-status`,
                      role: "switch",
                    }}
                  />
                </Stack>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Box>

      <Box py={2}>
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Box sx={{ width: "100%" }}>
            <Typography variant="subtitle1">
              {translate("consultant_profile_url")}
            </Typography>
            <Stack direction="row" spacing={1} alignItems="center">
              <Link
                // href={`${FRONTEND_END_POINT}profile/${consultantInfo._id}`}
                color="primary.main"
                target="_blank"
                className="text-decoration-none"
              >
                View Your Profile
              </Link>
              <CopyToClipboard
                options={{ message: "Whoa!" }}
                // onCopy={() => toast.success("Copied Successfully")}
                // text={`${FRONTEND_END_POINT}profile/${consultantInfo._id}`}
              >
                <IconButton color="primary">
                  <Iconify icon="eva:copy-fill" />
                </IconButton>
              </CopyToClipboard>
            </Stack>
          </Box>
          <SwitchToggle
            checked={get(consultantInfo, "profileUrlStatus", true)}
            onChange={(e) => handleStatus(e, "profileUrlStatus")}
            disabled={
              secName === "profileUrlStatus" ? loading || infoLoading : false
            }
            inputProps={{
              "aria-label": `profile-url-status`,
              role: "switch",
            }}
          />
        </Stack>
      </Box>

      <Box py={1}>
        <Stack
          direction="row"
          justifyContent="space-between"
          alignItems="center"
        >
          <Typography variant="subtitle1">
            {translate("consultant_about_me")}
          </Typography>
          <SwitchToggle
            checked={get(consultantInfo, "bioStatus", true)}
            onChange={(e) => handleStatus(e, "bioStatus")}
            disabled={secName === "bioStatus" ? loading || infoLoading : false}
            inputProps={{
              "aria-label": `bio-status`,
              role: "switch",
            }}
          />
        </Stack>
        <Typography variant="body2">{consultantInfo.bio}</Typography>
      </Box>
    </>
  );
};

export default TopDetails;
